import axios from 'axios';

export const handlePayment = async (amount, courseName = "Course Subscription") => {
  const res = await loadRazorpayScript();
  if (!res) {
    alert("Razorpay SDK failed to load.");
    return;
  }

  // Send amount to backend
  const { data: order } = await axios.post("/api/create-order", {
    amount: amount, // Send amount from frontend
    courseName: courseName // Optional: send course name for better tracking
  });

  const options = {
    key: "rzp_test_QF997RGW2waKt4", 
    amount: order.amount,
    currency: order.currency,
    name: "Option Hedge Academy",
    description: courseName,
    order_id: order.id,
    handler: async function (response) {
      toast.success("Payment Successful! " + response.razorpay_payment_id);

      await axios.post("/api/payment-success", {
        paymentId: response.razorpay_payment_id,
        orderId: response.razorpay_order_id,
        signature: response.razorpay_signature,
      });
    },  
    prefill: {
      name: "Trader",
      email: "theoptionhedge@gmail.com",
      contact: "+91 95686 08604",
    },
    theme: {
      color: "#7F00FF",
    },
  };

  const rzp = new window.Razorpay(options);
  rzp.open();
};

const loadRazorpayScript = () => {
  return new Promise((resolve) => {
    const script = document.createElement("script");
    script.src = "https://checkout.razorpay.com/v1/checkout.js";
    script.onload = () => resolve(true);
    script.onerror = () => resolve(false);
    document.body.appendChild(script);
  });
};