import React, { useState, useRef } from 'react';
import { Check, BookOpen, TrendingUp, Award, Play, Users, Clock, Star, ChevronLeft, ChevronRight } from 'lucide-react';
import { useNavigate } from 'react-router-dom';
const Card = ({ course }) => {
  const [isHovered, setIsHovered] = useState(false);

  const levelColors = {
    Beginner: 'from-green-500 to-emerald-500',
    Intermediate: 'from-blue-500 to-purple-500',
    Expert: 'from-yellow-500 to-orange-500'
  };
  const navigate = useNavigate();
  
  const handleclick = () => {
    navigate(`/enroll`);  
  };
  const levelBadgeColors = {
    Beginner: 'bg-green-500/20 text-green-400',
    Intermediate: 'bg-blue-500/20 text-blue-400',
    Expert: 'bg-yellow-500/20 text-yellow-400'
  };

  const levelIcons = {
    Beginner: BookOpen,
    Intermediate: TrendingUp,
    Expert: Award
  };

  const Icon = levelIcons[course.level];

  return (
    <div 
      className="flex-shrink-0 w-96 group relative"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/* Glow Effect */}
      <div className={`absolute inset-0 bg-gradient-to-r ${levelColors[course.level]}/20 rounded-2xl blur-xl transition-all duration-500 ${isHovered ? 'opacity-100 blur-2xl' : 'opacity-0'}`}></div>
      
      {/* Card Content - Horizontal Layout */}
      <div className={`relative bg-gradient-to-br from-slate-800/90 to-slate-900/90 backdrop-blur-lg rounded-2xl border border-white/10 transition-all duration-500 overflow-hidden ${isHovered ? 'border-purple-500/30 transform scale-105' : ''}`}>
        
        {/* Course Image/Icon Section */}
        <div className={`h-48 bg-gradient-to-br ${levelColors[course.level]} p-8 flex flex-col justify-between relative overflow-hidden`}>
          <div className="absolute top-0 right-0 w-32 h-32 bg-white/10 rounded-full -translate-y-16 translate-x-16"></div>
          <div className="absolute bottom-0 left-0 w-24 h-24 bg-white/10 rounded-full translate-y-12 -translate-x-12"></div>
          
          <div className="flex items-center justify-between relative z-10">
            <span className="px-3 py-1 bg-white/20 text-white rounded-full text-sm font-medium">
              {course.level}
            </span>
            <div className="w-12 h-12 bg-white/20 rounded-lg flex items-center justify-center">
              <Icon className="w-6 h-6 text-white" />
            </div>
          </div>
          
          <div className="relative z-10">
            <h3 className="text-xl font-bold text-white mb-2">{course.title}</h3>
            <div className="flex items-center space-x-4 text-white/80 text-sm">
              <div className="flex items-center space-x-1">
                <Clock className="w-4 h-4" />
                <span>{course.duration}</span>
              </div>
              <div className="flex items-center space-x-1">
                <Play className="w-4 h-4" />
                <span>{course.lessons} Sessions</span>
              </div>
            </div>
          </div>
        </div>

        {/* Course Details Section */}
        <div className="p-6">
          <p className="text-gray-400 leading-relaxed mb-4 text-sm">{course.description}</p>

          {/* Pricing */}
          <div className="flex items-baseline mb-4">
            <span className="text-2xl font-black text-purple-400">Rs. {course.price}</span>
            {course.originalPrice && (
              <span className="text-gray-500 ml-2 line-through text-lg">Rs. {course.originalPrice}</span>
            )}
          </div>

          {/* Course Stats */}
          <div className="flex items-center justify-between mb-4 text-sm text-gray-400">
            <div className="flex items-center space-x-1">
              <Users className="w-4 h-4" />
              <span>{course.students}+ Students</span>
            </div>
            <div className="flex items-center space-x-1">
              {[...Array(5)].map((_, i) => (
                <Star key={i} className={`w-3 h-3 ${i < course.rating ? 'text-yellow-400 fill-yellow-400' : 'text-gray-600'}`} />
              ))}
              <span className="ml-1">({course.reviews})</span>
            </div>
          </div>

          {/* Features - Condensed */}
          <div className="space-y-2 mb-6">
            {course.features.slice(0, 3).map((feature, index) => (
              <div key={index} className="flex items-center space-x-2">
                <div className="w-4 h-4 bg-purple-500 rounded-full flex items-center justify-center flex-shrink-0">
                  <Check className="w-2.5 h-2.5 text-white" />
                </div>
                <span className="text-gray-300 text-sm">{feature}</span>
              </div>
            ))}
            {course.features.length > 3 && (
              <div className="text-purple-400 text-sm">+{course.features.length - 3} more features</div>
            )}
          </div>

          {/* CTA Button */}
          <button className="w-full bg-gradient-to-r from-purple-500 to-pink-500 py-3 rounded-xl text-white font-semibold hover:shadow-lg hover:shadow-purple-500/25 transition-all duration-300 transform hover:scale-105" onClick={handleclick}>
            Enroll Now
          </button>
        </div>
      </div>
    </div>
  );
};

export default Card
