import React, { useState, useEffect } from "react";
import {
  Mail,
  MessageCircle,
  Send,
  Phone,
  MapPin,
  MessageCircleMore,
  Linkedin,
  Instagram,
} from "lucide-react";
import axios from "axios";
import { toast } from "react-toastify";
import {Link} from "react-router-dom";
const ContactPage = () => {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    message: "",
    number: "",
  });
  const [focusedField, setFocusedField] = useState("");
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    setIsVisible(true);
  }, []);

  const handleInputChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const Response = await axios.post("/api/contact", formData, {
        headers: {
          "Content-Type": "application/json",
        },
      });
      console.log("Response from server:", Response);
      if (Response.status === 200) {
        toast.success("Message sent successfully!");
        setFormData({ name: "", email: "", message: "", number: "" });
      } else {
        toast.error("Failed to send message. Please try again.");
      }
    } catch (e) {
      console.error("Error submitting form:", e);
      return;
    }
    console.log("Form submitted:", formData);
    // Handle form submission here
  };

  return (
    <div className="min-h-screen bg-black text-white overflow-hidden">
      <div
        className={`transition-all duration-1000 ${
          isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-10"
        }`}
      >
        {/* Hero Section */}
        <div className="relative z-10 container mx-auto px-6 pt-20 pb-16">
          <div className="text-center">
            <h1 className="text-6xl md:text-8xl font-black mb-6 leading-tight">
              <span className="bg-gradient-to-r from-purple-400 via-pink-400 to-cyan-400 bg-clip-text text-transparent">
                Get in Touch
              </span>
            </h1>
            <p className="text-xl text-gray-300 max-w-2xl mx-auto">
              Ready to transform your trading journey? Let's connect and discuss
              how we can help you achieve your financial goals.
            </p>
          </div>
        </div>
        {/* Main Content */}
        <div className="relative z-10 container mx-auto px-6 pb-20">
          <div className="max-w-7xl mx-auto">
            <div className="grid lg:grid-cols-2 gap-16 items-start">
              {/* Contact Form - Innovative Design */}
              <div className="relative">
                <div className="absolute inset-0 bg-gradient-to-br from-purple-600/20 to-cyan-600/20 rounded-3xl blur-xl"></div>
                <div className="relative bg-gray-900/60 backdrop-blur-2xl rounded-3xl p-8 border border-white/10">
                  <div className="mb-8">
                    <h2 className="text-3xl font-bold mb-2 bg-gradient-to-r from-purple-400 to-cyan-400 bg-clip-text text-transparent">
                      Send us a Message
                    </h2>
                    <p className="text-gray-400">
                      We'll get back to you within 24 hours
                    </p>
                  </div>
                  <form onSubmit={handleSubmit} className="space-y-6">
                    {/* Name Field */}
                    <div className="relative group">
                      <div
                        className={`absolute inset-0 bg-gradient-to-r from-purple-500/20 to-pink-500/20 rounded-2xl blur-sm transition-all duration-300 ${
                          focusedField === "name" ? "opacity-100" : "opacity-0"
                        }`}
                      ></div>
                      <div className="relative">
                        <input
                          type="text"
                          name="name"
                          value={formData.name}
                          onChange={handleInputChange}
                          onFocus={() => setFocusedField("name")}
                          onBlur={() => setFocusedField("")}
                          className="w-full bg-gray-800/50 border border-gray-700 rounded-2xl px-6 py-4 text-white placeholder-gray-400 focus:outline-none focus:border-purple-400 transition-all duration-300 peer"
                          placeholder=" "
                          required
                        />
                        <label className="absolute left-6 top-4 text-gray-400 transition-all duration-300 peer-focus:-translate-y-8 peer-focus:scale-75 peer-focus:text-purple-400 peer-valid:-translate-y-8 peer-valid:scale-75">
                          Your Name
                        </label>
                      </div>
                    </div>
                    {/* Email Field */}
                    <div className="relative group">
                      <div
                        className={`absolute inset-0 bg-gradient-to-r from-cyan-500/20 to-blue-500/20 rounded-2xl blur-sm transition-all duration-300 ${
                          focusedField === "email" ? "opacity-100" : "opacity-0"
                        }`}
                      ></div>
                      <div className="relative">
                        <input
                          type="email"
                          name="email"
                          value={formData.email}
                          onChange={handleInputChange}
                          onFocus={() => setFocusedField("email")}
                          onBlur={() => setFocusedField("")}
                          className="w-full bg-gray-800/50 border border-gray-700 rounded-2xl px-6 py-4 text-white placeholder-gray-400 focus:outline-none focus:border-cyan-400 transition-all duration-300 peer"
                          placeholder=" "
                          required
                        />
                        <label className="absolute left-6 top-4 text-gray-400 transition-all duration-300 peer-focus:-translate-y-8 peer-focus:scale-75 peer-focus:text-cyan-400 peer-valid:-translate-y-8 peer-valid:scale-75">
                          Email Address
                        </label>
                      </div>
                    </div>

                    <div className="relative group">
                      <div
                        className={`absolute inset-0 bg-gradient-to-r from-cyan-500/20 to-blue-500/20 rounded-2xl blur-sm transition-all duration-300 ${
                          focusedField === "email" ? "opacity-100" : "opacity-0"
                        }`}
                      ></div>
                      <div className="relative">
                        <input
                          type="number"
                          name="number"
                          value={formData.number}
                          onChange={handleInputChange}
                          onFocus={() => setFocusedField("number")}
                          onBlur={() => setFocusedField("")}
                          className="w-full bg-gray-800/50 border border-gray-700 rounded-2xl px-6 py-4 text-white placeholder-gray-400 focus:outline-none focus:border-cyan-400 transition-all duration-300 peer"
                          placeholder=" "
                          required
                        />
                        <label className="absolute left-6 top-4 text-gray-400 transition-all duration-300 peer-focus:-translate-y-8 peer-focus:scale-75 peer-focus:text-cyan-400 peer-valid:-translate-y-8 peer-valid:scale-75">
                          Mobile Number
                        </label>
                      </div>
                    </div>
                    {/* Message Field */}
                    <div className="relative group">
                      <div
                        className={`absolute inset-0 bg-gradient-to-r from-pink-500/20 to-purple-500/20 rounded-2xl blur-sm transition-all duration-300 ${
                          focusedField === "message"
                            ? "opacity-100"
                            : "opacity-0"
                        }`}
                      ></div>
                      <div className="relative">
                        <textarea
                          name="message"
                          value={formData.message}
                          onChange={handleInputChange}
                          onFocus={() => setFocusedField("message")}
                          onBlur={() => setFocusedField("")}
                          rows="6"
                          className="w-full bg-gray-800/50 border border-gray-700 rounded-2xl px-6 py-4 text-white placeholder-gray-400 focus:outline-none focus:border-pink-400 transition-all duration-300 peer resize-none"
                          placeholder=" "
                          required
                        ></textarea>
                        <label className="absolute left-6 top-4 text-gray-400 transition-all duration-300 peer-focus:-translate-y-8 peer-focus:scale-75 peer-focus:text-pink-400 peer-valid:-translate-y-8 peer-valid:scale-75">
                          Your Message
                        </label>
                      </div>
                    </div>
                    {/* Submit Button */}
                    <div className="relative group">
                      {/* <div className="absolute inset-0 bg-gradient-to-r from-purple-600 to-cyan-600 rounded-2xl blur-sm group-hover:blur-md transition-all duration-300"></div> */}
                      <button
                        type="submit"
                        className="relative w-full bg-gradient-to-r from-purple-600 to-cyan-600 hover:from-purple-500 hover:to-cyan-500 text-white font-bold py-4 px-8 rounded-2xl transition-all duration-300 transform hover:scale-105 flex items-center justify-center space-x-2"
                      >
                        <Send className="w-5 h-5" />
                        <span>Send Message</span>
                      </button>
                    </div>
                  </form>
                </div>
              </div>
              {/* Contact Info & Alternative Methods */}
              <div className="space-y-8">
                {/* Contact Cards */}
                <div className="grid gap-6">
                  <div className="relative group">
                    <div className="absolute inset-0 bg-gradient-to-br from-purple-600/20 to-pink-600/20 rounded-3xl blur-xl group-hover:blur-2xl transition-all duration-500"></div>
                    <div className="relative bg-gray-900/60 backdrop-blur-2xl rounded-3xl p-6 border border-purple-500/20 hover:border-purple-400/40 transition-all duration-500">
                      <div className="flex items-center space-x-4">
                        <div className="w-12 h-12 bg-gradient-to-br from-purple-500 to-pink-500 rounded-2xl flex items-center justify-center">
                          <Mail className="w-6 h-6 text-white" />
                        </div>
                        <div>
                          <h3 className="text-xl font-bold text-white mb-1">
                            Email Us
                          </h3>
                          <p className="text-purple-300">
                            theoptionhedge@gmail.com
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="relative group">
                    <div className="absolute inset-0 bg-gradient-to-br from-cyan-600/20 to-blue-600/20 rounded-3xl blur-xl group-hover:blur-2xl transition-all duration-500"></div>
                    <div className="relative bg-gray-900/60 backdrop-blur-2xl rounded-3xl p-6 border border-cyan-500/20 hover:border-cyan-400/40 transition-all duration-500">
                      <div className="flex items-center space-x-4">
                        <div className="w-12 h-12 bg-gradient-to-br from-cyan-500 to-blue-500 rounded-2xl flex items-center justify-center">
                          <Phone className="w-6 h-6 text-white" />
                        </div>
                        <div>
                          <h3 className="text-xl font-bold text-white mb-1">
                            Call Us
                          </h3>
                          <p className="text-cyan-300">+91 81717 60244</p>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="relative group">
                    <div className="absolute inset-0 bg-gradient-to-br from-pink-600/20 to-purple-600/20 rounded-3xl blur-xl group-hover:blur-2xl transition-all duration-500"></div>
                    <div className="relative bg-gray-900/60 backdrop-blur-2xl rounded-3xl p-6 border border-pink-500/20 hover:border-pink-400/40 transition-all duration-500">
                      <div className="flex items-center space-x-4">
                        <div className="w-12 h-12 bg-gradient-to-br from-pink-500 to-purple-500 rounded-2xl flex items-center justify-center">
                          <MapPin className="w-6 h-6 text-white" />
                        </div>
                        <div>
                          <h3 className="text-xl font-bold text-white mb-1">
                            Address
                          </h3>
                          <p className="text-pink-300">Sector 1, Greater Noida, India</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                {/* Social Media */}
                <div className="relative">
                  <div className="absolute inset-0 bg-gradient-to-br from-gray-600/20 to-gray-800/20 rounded-3xl blur-xl"></div>
                  <div className="relative bg-gray-900/60 backdrop-blur-2xl rounded-3xl p-8 border border-white/10">
                    <h3 className="text-2xl font-bold text-white mb-6 text-center">
                      Follow Us
                    </h3>
                    <div className="flex justify-center space-x-6">
                      <Link to="https://chat.whatsapp.com/I8QlM11wfOcE8Xj7F2BPI8" className="group">
                        <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-blue-600 rounded-2xl flex items-center justify-center group-hover:scale-110 transition-transform duration-300">
                          <MessageCircle className="w-6 h-6 text-white" />
                        </div>
                      </Link>
                      <Link to="https://www.linkedin.com/company/theoptionhedge" className="group">
                        <div className="w-12 h-12 bg-gradient-to-br from-blue-600 to-blue-700 rounded-2xl flex items-center justify-center group-hover:scale-110 transition-transform duration-300">
                          <Linkedin className="w-6 h-6 text-white" />
                        </div>
                      </Link>
                      <Link to="https://www.instagram.com/theoptionhedge/" className="group">
                        <div className="w-12 h-12 bg-gradient-to-br from-pink-500 to-purple-600 rounded-2xl flex items-center justify-center group-hover:scale-110 transition-transform duration-300">
                          <Instagram className="w-6 h-6 text-white" />
                        </div>
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
       
      </div>
    </div>
  );
};

export default ContactPage;
