import React, { useState, useEffect } from "react";
import { User, Menu, X } from "lucide-react";
import logo from "../../assets/logo.png";
import axios from "axios";
import { toast } from "react-toastify";
const Navbar = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isVisible, setIsVisible] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [user, setUser] = useState(null);

  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;

      setIsScrolled(currentScrollY > 20);

      if (currentScrollY > lastScrollY && currentScrollY > 50) {
        setIsVisible(false);
      } else {
        setIsVisible(true);
      }

      setLastScrollY(currentScrollY);
    };

    window.addEventListener("scroll", handleScroll);

    return () => window.removeEventListener("scroll", handleScroll);
  }, [lastScrollY]);

  const logout = async () => {
    try {
      // Simulated logout for demo - replace with actual axios call
      console.log("Logout functionality");
      setUser(null);
      toast.success("Logged out successfully");
      window.location.href = '/';
    } catch (error) {
      console.error("Logout failed", error);
    }
  };

  useEffect(() => {
    const fetchUser = async () => {

      const response = await axios.get("/api/current");
      if (response.data) {
          console.log("User data fetched:", response.data);
          setUser(response.data);
      }
    };
    fetchUser();
  }, []);

  const logoutUser = async () => {
    try {
      await axios.get("/api/logout");
      setUser(null);
      toast.success("Logged out successfully");
      window.location.href = "/";
    } catch (error) {
      console.error("Logout failed", error);
    }
  };

  const navigationItems = [
    { label: "Home", href: "/" },
    { label: "Courses", href: "/courses" },
    { label: "About", href: "/about" },
    { label: "Contact", href: "/contact" },
  ];

  return (
    <nav
      className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 transform ${
        isScrolled
          ? "bg-slate-950/95 backdrop-blur-xl border-b border-white/10 shadow-2xl shadow-purple-500/10"
          : "bg-slate-950/80 backdrop-blur-lg border-b border-white/5"
      } ${isVisible ? "translate-y-0" : "-translate-y-full"}`}
    >
      <div className="max-w-7xl mx-auto px-6">
        <div className="flex items-center justify-between h-16 w-full space-x-6 flex-nowrap">
          {/* Logo */}
          <div className="flex items-center space-x-3 group">
            <div className="relative">
              <div className="w-10 h-10 rounded-xl flex items-center justify-center transform hover:scale-110 transition-all duration-300">
                <div className="w-8 h-8  rounded-lg flex items-center justify-center text-white font-bold">
                  <img src={logo} alt="Logo" className="w-6 h-6" />
                </div>
              </div>
              <div className="absolute inset-0 rounded-xl blur opacity-50 hover:opacity-70 transition-opacity duration-300"></div>
            </div>
            <h1 className="hidden sm:block text-xl font-bold transition-all duration-300 ease-in-out group-hover:-translate-y-1 group-hover:scale-105 text-white group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-purple-500 group-hover:to-pink-500">
              <a href="/">The Option Hedge Academy</a>
            </h1>
          </div>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center space-x-2">
            {navigationItems.map((item, index) => (
              <a
                key={index}
                href={item.href}
                className="px-4 py-2 rounded-lg text-gray-300 transition-all duration-200 font-medium hover:bg-clip-text hover:text-transparent hover:bg-gradient-to-r hover:from-purple-500 hover:to-pink-500"
              >
                {item.label}
              </a>
            ))}
          </div>

          {/* Desktop User Actions */}
          <div className="hidden md:flex items-center space-x-3">
            {user ? (
              <div className="text-gray-300 font-medium px-4 py-2">
                <User className="inline w-4 h-4 mr-2" />
                <button onClick={logoutUser} className="hover:text-purple-400 transition-colors duration-200">
                   Logout
                </button>
              </div>
            ) : (
              <a
                href="/login"
                className="flex items-center space-x-2 px-4 py-2 text-gray-300 rounded-lg transition-all duration-200 hover:bg-clip-text hover:text-transparent hover:bg-gradient-to-r hover:from-purple-500 hover:to-pink-500"
              >
                <User className="w-4 h-4" />
                <span className="font-medium">Sign In</span>
              </a>
            )}
            <a href="/enroll" className="relative group">
              <div className="absolute inset-0 bg-gradient-to-r from-purple-500 to-pink-500 rounded-xl blur opacity-50 group-hover:opacity-70 transition-opacity duration-300"></div>
              <div className="relative bg-gradient-to-r from-purple-500 to-pink-500 px-6 py-2 rounded-xl text-white font-semibold transform group-hover:scale-105 transition-all duration-300 shadow-lg shadow-purple-500/30">
                Enroll Now
              </div>
            </a>
          </div>

          {/* Mobile Menu Button */}
          <button
            className="md:hidden flex items-center justify-center p-2 rounded-lg text-gray-300 hover:bg-slate-800 focus:outline-none"
            onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
            aria-label="Toggle menu"
          >
            {mobileMenuOpen ? (
              <X className="w-6 h-6" />
            ) : (
              <Menu className="w-6 h-6" />
            )}
          </button>
        </div>
      </div>

      {/* Mobile Menu */}
      {mobileMenuOpen && (
        <div className="md:hidden bg-slate-950/95 backdrop-blur-xl border-b border-white/10 shadow-2xl shadow-purple-500/10 px-6 pb-6">
          <div className="flex flex-col space-y-2 mt-2">
            {/* Navigation Items */}
            {navigationItems.map((item, index) => (
              <a
                key={index}
                href={item.href}
                className="block px-4 py-3 rounded-lg text-gray-300 transition-all duration-200 font-medium hover:bg-clip-text hover:text-transparent hover:bg-gradient-to-r hover:from-purple-500 hover:to-pink-500"
                onClick={() => setMobileMenuOpen(false)}
              >
                {item.label}
              </a>
            ))}

            {/* Mobile User Actions */}
            {user ? (
              <div className="flex items-center space-x-2 px-4 py-3 text-gray-300">
                <User className="w-4 h-4" />
                <button 
                  onClick={() => {
                    logoutUser();
                  }}
                  className="font-medium hover:text-purple-400 transition-colors duration-200"
                >
                 Logout
                </button>
              </div>
            ) : (
              <a
                href="/login"
                className="flex items-center space-x-2 px-4 py-3 text-gray-300 rounded-lg transition-all duration-200 hover:bg-clip-text hover:text-transparent hover:bg-gradient-to-r hover:from-purple-500 hover:to-pink-500"
                onClick={() => setMobileMenuOpen(false)}
              >
                <User className="w-4 h-4" />
                <span className="font-medium">Sign In</span>
              </a>
            )}

            {/* Mobile Enroll Button */}
            <a
              href="/enroll"
              className="relative group mt-2 block"
              onClick={() => setMobileMenuOpen(false)}
            >
              <div className="absolute inset-0 bg-gradient-to-r from-purple-500 to-pink-500 rounded-xl blur opacity-50 group-hover:opacity-70 transition-opacity duration-300"></div>
              <div className="relative bg-gradient-to-r from-purple-500 to-pink-500 px-6 py-3 rounded-xl text-white font-semibold transform group-hover:scale-105 transition-all duration-300 shadow-lg shadow-purple-500/30 text-center">
                Enroll Now
              </div>
            </a>
          </div>
        </div>
      )}
    </nav>
  );
};

export default Navbar;