import React, { useState } from 'react';
import { Eye, EyeOff, User, Mail, Lock, Phone, AlertCircle, CheckCircle } from 'lucide-react';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
// Input Field Component
const InputField = ({ 
  type = "text", 
  placeholder, 
  value, 
  onChange, 
  icon: Icon, 
  className = "",
  showPasswordToggle = false,
  showPassword = false,
  onTogglePassword,
  ...props 
}) => {
  return (
    <div className="relative">
      {Icon && (
        <Icon className="absolute left-4 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
      )}
      <input
        type={showPasswordToggle ? (showPassword ? "text" : "password") : type}
        placeholder={placeholder}
        value={value}
        onChange={(e) => onChange(e.target.value)}
        className={`w-full bg-white/10 border border-white/20 rounded-xl py-3 ${
          Icon ? 'pl-12' : 'pl-4'
        } ${
          showPasswordToggle ? 'pr-12' : 'pr-4'
        } text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent transition-all duration-300 ${className}`}
        {...props}
      />
      {showPasswordToggle && (
        <button
          type="button"
          onClick={onTogglePassword}
          className="absolute right-4 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-white transition-colors"
        >
          {showPassword ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
        </button>
      )}
    </div>
  );
};

// Button Component
const Button = ({ 
  children, 
  onClick, 
  disabled = false, 
  variant = "primary", 
  className = "",
  ...props 
}) => {
  const baseClasses = "w-full font-semibold py-4 rounded-xl transition-all duration-300 transform disabled:opacity-70 disabled:cursor-not-allowed disabled:transform-none";
  
  const variants = {
    primary: "bg-gradient-to-r from-purple-600 to-pink-600 hover:from-purple-700 hover:to-pink-700 text-white hover:scale-105",
    secondary: "bg-white/10 hover:bg-white/20 text-white border border-white/20 hover:border-white/30"
  };

  return (
    <button
      onClick={onClick}
      disabled={disabled}
      className={`${baseClasses} ${variants[variant]} ${className}`}
      {...props}
    >
      {children}
    </button>
  );
};

// Google Button Component
const GoogleButton = ({ onClick, disabled = false, text = "Continue with Google" }) => {
  return (
    <button
      onClick={onClick}
      disabled={disabled}
      className="w-full bg-white hover:bg-gray-50 text-gray-700 font-semibold py-4 rounded-xl border border-gray-300 transition-all duration-300 transform hover:scale-105 disabled:opacity-70 disabled:cursor-not-allowed disabled:transform-none flex items-center justify-center gap-3"
    >
      <svg className="w-5 h-5" viewBox="0 0 24 24">
        <path fill="#4285F4" d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"/>
        <path fill="#34A853" d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"/>
        <path fill="#FBBC05" d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"/>
        <path fill="#EA4335" d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"/>
      </svg>
      {text}
    </button>
  );
};

// Divider Component
const Divider = ({ text = "or" }) => {
  return (
    <div className="flex items-center my-6">
      <div className="flex-1 border-t border-white/20"></div>
      <span className="px-4 text-gray-400 text-sm">{text}</span>
      <div className="flex-1 border-t border-white/20"></div>
    </div>
  );
};

// Message Component
const Message = ({ type, content, onClose }) => {
  if (!content) return null;

  const styles = {
    success: "bg-green-500/20 border-green-500/50 text-green-300",
    error: "bg-red-500/20 border-red-500/50 text-red-300",
    info: "bg-blue-500/20 border-blue-500/50 text-blue-300"
  };

  const icons = {
    success: CheckCircle,
    error: AlertCircle,
    info: AlertCircle
  };

  const Icon = icons[type];

  return (
    <div className={`mb-6 p-4 rounded-xl border flex items-center gap-3 ${styles[type]}`}>
      <Icon className="w-5 h-5 flex-shrink-0" />
      <span className="flex-1">{content}</span>
      {onClose && (
        <button onClick={onClose} className="text-current hover:opacity-70">
          
        </button>
      )}
    </div>
  );
};

// Tab Navigation Component
const TabNavigation = ({ activeTab, onTabChange, tabs }) => {
  return (
    <div className="flex bg-white/5 rounded-2xl p-1 mb-8">
      {tabs.map((tab) => (
        <button
          key={tab.id}
          onClick={() => onTabChange(tab.id)}
          className={`flex-1 py-3 px-4 rounded-xl transition-all duration-300 font-medium ${
            activeTab === tab.id
              ? 'bg-gradient-to-r from-purple-600 to-pink-600 text-white shadow-lg'
              : 'text-gray-300 hover:text-white'
          }`}
        >
          {tab.label}
        </button>
      ))}
    </div>
  );
};

// Login Form Component
const LoginForm = ({ formData, onChange, onSubmit, loading, showPassword, onTogglePassword, onGoogleLogin }) => {
  return (
    <div className="space-y-6">
      <GoogleButton 
        onClick={onGoogleLogin}
        disabled={loading}
        text="Continue with Google"
      />
      
      <Divider />
      
      <InputField
        type="email"
        placeholder="Email"
        value={formData.username}
        onChange={(value) => onChange('username', value)}
        icon={Mail}
      />
      
      <InputField
        placeholder="Password"
        value={formData.password}
        onChange={(value) => onChange('password', value)}
        icon={Lock}
        showPasswordToggle={true}
        showPassword={showPassword}
        onTogglePassword={onTogglePassword}
      />
      
      <Button onClick={onSubmit} disabled={loading}>
        {loading ? 'Logging in...' : 'Login'}
      </Button>
    </div>
  );
};

// Signup Form Component
const SignupForm = ({ 
  formData, 
  onChange, 
  onSubmit, 
  loading, 
  showPassword, 
  onTogglePassword,
  termsAccepted,
  onTermsChange,
  onGoogleLogin
}) => {
  return (
    <div className="space-y-4">
      <GoogleButton 
        onClick={onGoogleLogin}
        disabled={loading}
        text="Sign up with Google"
      />
      
      <Divider />
      
      <InputField
        type="email"
        placeholder="Email"
        value={formData.email}
        onChange={(value) => onChange('email', value)}
        icon={Mail}
      />

      <InputField
        type="tel"
        placeholder="Phone Number"
        value={formData.phone}
        onChange={(value) => onChange('phone', value)}
        icon={Phone}
      />
      
      <InputField
        placeholder="City"
        value={formData.city}
        onChange={(value) => onChange('city', value)}
      />
      
      <InputField
        placeholder="Password"
        value={formData.password}
        onChange={(value) => onChange('password', value)}
        icon={Lock}
        showPasswordToggle={true}
        showPassword={showPassword}
        onTogglePassword={onTogglePassword}
      />
      
      <InputField
        type="password"
        placeholder="Confirm Password"
        value={formData.confirmPassword}
        onChange={(value) => onChange('confirmPassword', value)}
        icon={Lock}
      />

      <div className="flex items-center space-x-2">
        <input
          type="checkbox"
          id="termsAndConditions"
          checked={termsAccepted}
          onChange={onTermsChange}
          className="w-4 h-4 text-purple-600 bg-white/10 border-white/20 rounded focus:ring-purple-500"
        />
        <label htmlFor="termsAndConditions" className="text-sm text-gray-300 cursor-pointer">
          I agree to the{' '}
          <Link
            to="/terms" 
            className="text-purple-400 hover:text-purple-300 underline"
          >
            terms and conditions
          </Link>
        </label>
      </div>

      <Button onClick={onSubmit} disabled={loading}>
        {loading ? 'Creating Account...' : 'Create Account'}
      </Button>
    </div>
  );
};

// Header Component
const AuthHeader = ({ title, subtitle }) => {
  return (
    <div className="text-center mb-8">
      <h1 className="text-3xl font-bold bg-gradient-to-r from-purple-400 to-pink-400 bg-clip-text text-transparent">
        {title}
      </h1>
      {subtitle && (
        <p className="text-gray-300 mt-2">{subtitle}</p>
      )}
    </div>
  );
};

// Background Component
const AuthBackground = () => {
  return (
    <div className="absolute inset-0 overflow-hidden">
      <div className="absolute -top-40 -right-40 w-80 h-80 bg-purple-700 rounded-full mix-blend-multiply filter blur-xl opacity-20 animate-pulse"></div>
      <div className="absolute -bottom-40 -left-40 w-80 h-80 bg-blue-900 rounded-full mix-blend-multiply filter blur-xl opacity-20 animate-pulse" style={{animationDelay: '1s'}}></div>
    </div>
  );
};

// Main Authentication Container Component
const AuthContainer = ({ children }) => {
  return (
    <div className="relative bg-white/10 backdrop-blur-lg border border-white/20 rounded-3xl p-8 w-full max-w-md shadow-2xl">
      <div className="absolute inset-0 rounded-3xl bg-gradient-to-r from-purple-600 via-pink-600 to-blue-600 opacity-20 blur-sm pointer-events-none" 
           style={{animation: 'spin 20s linear infinite'}}></div>
      <div className="relative z-10">
        {children}
      </div>
    </div>
  );
};

// Main Authentication Page Component
const AuthPage = () => {
  const [activeTab, setActiveTab] = useState('login');
  const [showPassword, setShowPassword] = useState(false);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState({ type: '', content: '' });
  const [termsAccepted, setTermsAccepted] = useState(false);

  const [loginData, setLoginData] = useState({
    username: '',
    password: ''
  });

  const [signupData, setSignupData] = useState({
    email: '',
    password: '',
    confirmPassword: '',
    city: '',
    phone: ''
  });

  const tabs = [
    { id: 'login', label: 'Login' },
    { id: 'signup', label: 'Sign Up' }
  ];

  const validateEmail = (email) => {
    return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
  };

  const handleInputChange = (formType, field, value) => {
    if (formType === 'login') {
      setLoginData(prev => ({ ...prev, [field]: value }));
    } else {
      setSignupData(prev => ({ ...prev, [field]: value }));
    }
    if (message.content) setMessage({ type: '', content: '' });
  };

  const handleGoogleLogin = () => {
    setLoading(true);
    // Redirect to Google OAuth endpoint
    window.location.href = '/api/auth/google';
   toast.info('Redirecting to Google login...');
  };

  const handleLogin = async () => {
    setLoading(true);
    setMessage({ type: '', content: '' });

    if (!loginData.username || !loginData.password) {
      setMessage({ type: 'error', content: 'Please fill in all fields' });
      setLoading(false);
      return;
    }

    if (!validateEmail(loginData.username)) {
      setMessage({ type: 'error', content: 'Please enter a valid email address' });
      setLoading(false);
      return;
    }

    try {
      const response = await fetch('/api/login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(loginData),
      });

      const data = await response.json();

      if (response.ok) {
        toast.success('Login successful!');
        setMessage({ type: 'success', content: 'Login successful!' });
        setTimeout(() => {
          window.location.href = '/enroll';
        }, 1500);
      } else {
        setMessage({ type: 'error', content: data.error || 'Login failed' });
      }
    } catch (error) {
      setMessage({ type: 'error', content: 'Network error. Please try again.' });
    } finally {
      setLoading(false);
    }
  };

  const handleSignup = async () => {
    setLoading(true);
    setMessage({ type: '', content: '' });

    if (!signupData.email || !signupData.password 
        || !signupData.city || !signupData.phone) {
      setMessage({ type: 'error', content: 'Please fill in all fields' });
      setLoading(false);
      return;
    }

    if (!validateEmail(signupData.email)) {
      setMessage({ type: 'error', content: 'Please enter a valid email address' });
      setLoading(false);
      return;
    }

    if (signupData.password.length < 6) {
      setMessage({ type: 'error', content: 'Password must be at least 6 characters long' });
      setLoading(false);
      return;
    }

    if (signupData.password !== signupData.confirmPassword) {
      setMessage({ type: 'error', content: 'Passwords do not match' });
      setLoading(false);
      return;
    }

    if (!termsAccepted) {
      setMessage({ type: 'error', content: 'Please agree to the terms and conditions' });
      setLoading(false);
      return;
    }

    try {
      const { confirmPassword, ...dataToSend } = signupData;
      const response = await fetch('/api/signup', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(dataToSend),
      });

      const data = await response.json();

      if (response.ok) {
        toast.success('Account created successfully!');
        setMessage({ type: 'success', content: 'Account created successfully!' });
        setTimeout(() => {
          window.location.href = '/enroll';
          setSignupData({
            email: '', 
            password: '', 
            confirmPassword: '',
            city: '', 
            phone: ''
          });
          setTermsAccepted(false);
        }, 2000);
      } else {
        setMessage({ type: 'error', content: data.error || 'Signup failed' });
      }
    } catch (error) {
      setMessage({ type: 'error', content: 'Network error. Please try again.' });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen pt-16 bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900 flex items-center justify-center p-4 overflow-hidden relative">
      <AuthBackground />
      
      <AuthContainer>
        <AuthHeader 
          title="Option Hedge Academy"
          subtitle="Master the art of options trading"
        />
        
        <TabNavigation 
          activeTab={activeTab}
          onTabChange={setActiveTab}
          tabs={tabs}
        />
        
        <Message 
          type={message.type}
          content={message.content}
          onClose={() => setMessage({ type: '', content: '' })}
        />
        
        {activeTab === 'login' ? (
          <LoginForm
            formData={loginData}
            onChange={(field, value) => handleInputChange('login', field, value)}
            onSubmit={handleLogin}
            loading={loading}
            showPassword={showPassword}
            onTogglePassword={() => setShowPassword(!showPassword)}
            onGoogleLogin={handleGoogleLogin}
          />
        ) : (
          <SignupForm
            formData={signupData}
            onChange={(field, value) => handleInputChange('signup', field, value)}
            onSubmit={handleSignup}
            loading={loading}
            showPassword={showPassword}
            onTogglePassword={() => setShowPassword(!showPassword)}
            termsAccepted={termsAccepted}
            onTermsChange={(e) => setTermsAccepted(e.target.checked)}
            onGoogleLogin={handleGoogleLogin}
          />
        )}
      </AuthContainer>
      
      <style jsx>{`
        @keyframes spin {
          from { transform: rotate(0deg); }
          to { transform: rotate(360deg); }
        }
      `}</style>
    </div>
  );
};

export default AuthPage;