import React, { useState, useEffect } from 'react';
import { Star, Users, Clock, BookOpen, TrendingUp, Shield, BarChart3, DollarSign } from 'lucide-react';
import { handlePayment } from '../../utils/razorpay';
import { Link } from 'react-router-dom';
import axios from 'axios';

const CourseCard = ({ course, isActive, onClick }) => {
  const [isHovered, setIsHovered] = useState(false);
  const [user, setUser] = useState(null);

  useEffect(() => {
    const fetchUser = async () => {
      const response = await axios.get("/api/current");
      if (response.data.user) {
        console.log(response.data.user);
        setUser(response.data.user);
        console.log(user);
      }
    };
    fetchUser();
  }, []);

  // Extract numeric value from course.price (e.g., "9,499" -> 9499)
  const getNumericPrice = (price) => {
    return parseInt(price.replace(/[^\d]/g, ''));
  };

  const handleCoursePayment = () => {
    const numericPrice = getNumericPrice(course.price);
    handlePayment(numericPrice, course.title);
  };

  return (
    <div 
      className={`relative overflow-hidden rounded-2xl md:rounded-3xl transition-all duration-500 cursor-pointer transform ${
        isActive ? 'scale-105 shadow-2xl' : 'scale-100 shadow-lg hover:shadow-xl'
      } ${isHovered ? 'scale-102' : ''}`}
      style={{
        background: `linear-gradient(135deg, ${course.gradient.from} 0%, ${course.gradient.to} 100%)`,
        minHeight: '320px'
      }}
      onClick={onClick}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      <div className="absolute inset-0 opacity-10">
        <div className="absolute top-5 left-5 md:top-10 md:left-10 w-16 h-16 md:w-32 md:h-32 bg-white rounded-full animate-pulse"></div>
        <div className="absolute bottom-5 right-5 md:bottom-10 md:right-10 w-12 h-12 md:w-24 md:h-24 bg-white rounded-full animate-pulse delay-300"></div>
        <div className="absolute top-1/2 right-10 md:right-20 w-8 h-8 md:w-16 md:h-16 bg-white rounded-full animate-pulse delay-700"></div>
      </div>
      <div className="absolute inset-0 bg-gradient-to-br from-white/10 to-transparent backdrop-blur-sm"></div>

      <div className="relative p-4 md:p-6 lg:p-8 h-full flex flex-col lg:flex-row">
        <div className="flex-1 flex flex-col justify-between text-white z-10">
          <div>
            <div className="flex items-center mb-3 md:mb-4">
              <course.icon className="w-6 h-6 md:w-8 md:h-8 mr-2 md:mr-3" />
              <span className="text-xs md:text-sm font-medium bg-white/20 px-2 md:px-3 py-1 rounded-full backdrop-blur-sm">
                {course.category}
              </span>
            </div>
            
            <h2 className="text-2xl md:text-3xl lg:text-4xl font-bold mb-3 md:mb-4 leading-tight">
              {course.title}
            </h2>
            
            <p className="text-sm md:text-base lg:text-lg opacity-90 mb-4 md:mb-6 lg:mb-8 leading-relaxed">
              {course.description}
            </p>

            <div className="grid grid-cols-1 sm:grid-cols-3 gap-3 md:gap-4 lg:flex lg:items-center lg:space-x-8 lg:gap-0 mb-4 md:mb-6 lg:mb-8">
              <div className="flex items-center">
                <Star className="w-4 h-4 md:w-5 md:h-5 text-yellow-300 fill-current mr-2" />
                <span className="text-lg md:text-xl lg:text-2xl font-bold">{course.rating}</span>
                <span className="text-xs md:text-sm opacity-75 ml-2 hidden sm:inline">avg rating</span>
              </div>
              
              <div className="flex items-center">
                <BookOpen className="w-4 h-4 md:w-5 md:h-5 mr-2" />
                <span className="text-lg md:text-xl lg:text-2xl font-bold">{course.exercises}</span>
                <span className="text-xs md:text-sm opacity-75 ml-2 hidden sm:inline">sessions</span>
              </div>
              
              <div className="flex items-center">
                <Clock className="w-4 h-4 md:w-5 md:h-5 mr-2" />
                <span className="text-lg md:text-xl lg:text-2xl font-bold">{course.hours}</span>
                <span className="text-xs md:text-sm opacity-75 ml-2 hidden sm:inline">hours</span>
              </div>
            </div>

            {user ? (
              <div className="flex flex-col sm:flex-row sm:items-center space-y-3 sm:space-y-0 sm:space-x-4">
                <button 
                  onClick={handleCoursePayment} 
                  className="bg-white text-gray-900 px-4 md:px-6 lg:px-8 py-3 md:py-4 rounded-xl md:rounded-2xl font-semibold text-sm md:text-base lg:text-lg transition-all duration-300 hover:bg-gray-100 hover:scale-105 hover:shadow-lg"
                >
                  Buy Live @ {course.price}
                </button>
                <div className="text-xs md:text-sm opacity-75">
                  Original Price 24999. Discounted at {course.price}
                </div>
              </div>
            ) : (
              <div className="flex flex-col sm:flex-row sm:items-center space-y-3 sm:space-y-0 sm:space-x-4">
                <Link to='/login'>
                  <button className="bg-white text-gray-900 px-4 md:px-6 lg:px-8 py-3 md:py-4 rounded-xl md:rounded-2xl font-semibold text-sm md:text-base lg:text-lg transition-all duration-300 hover:bg-gray-100 hover:scale-105 hover:shadow-lg">
                    Buy Live @ {course.price}
                  </button>
                </Link>
                <div className="text-xs md:text-sm opacity-75">
                  Original Price 24999. Discounted at {course.price}
                </div>
              </div>
            )}
          </div>

          <div className="flex items-center mt-4 md:mt-6">
            <Users className="w-4 h-4 md:w-5 md:h-5 mr-2" />
            <span className="text-xs md:text-sm opacity-75">
              <strong>{course.enrolled}</strong> learners enrolled
            </span>
          </div>
        </div>

        <div className="hidden lg:flex w-64 xl:w-80 items-center justify-center relative mt-6 lg:mt-0">
          <div className={`transition-transform duration-500 ${isHovered ? 'scale-110 rotate-3' : 'scale-100'}`}>
            <div className="w-48 h-48 xl:w-64 xl:h-64 bg-white/20 rounded-2xl xl:rounded-3xl flex items-center justify-center backdrop-blur-sm border border-white/30">
              <course.icon className="w-24 h-24 xl:w-32 xl:h-32 text-white" />
            </div>
          </div>
          <div className="absolute top-10 -right-5 bg-white/20 backdrop-blur-sm rounded-2xl p-3 animate-bounce delay-1000">
            <TrendingUp className="w-6 h-6 text-white" />
          </div>
          <div className="absolute bottom-10 -left-5 bg-white/20 backdrop-blur-sm rounded-2xl p-3 animate-bounce delay-500">
            <DollarSign className="w-6 h-6 text-white" />
          </div>
        </div>

        <div className="flex lg:hidden justify-center mt-4">
          <div className="w-16 h-16 md:w-20 md:h-20 bg-white/20 rounded-xl flex items-center justify-center backdrop-blur-sm border border-white/30">
            <course.icon className="w-8 h-8 md:w-10 md:h-10 text-white" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default CourseCard;