import React, { useState, useEffect } from 'react';
import { Check, BookOpen, TrendingUp, Award, Play, Users, Clock, Star, ChevronLeft, ChevronRight, Link } from 'lucide-react';
import Card from '../components/ui/Card';
import CourseSlider from '../components/ui/CourseSlider';
import { Link as ReactRouterLink} from 'react-router-dom'; // Ensure you have react-router-dom installed
// import '../styles/globals.css'; // Ensure global styles are imported

const CoursesPage = () => {
  const courses = [
    {
      id: 2,
      title: "Advanced Options Strategies",
      description: "Take your trading to the next level with complex strategies and advanced techniques for experienced traders.",
      price: 9499,
      originalPrice: 24999,
      level: "Intermediate",
      duration: "4 weeks",
      lessons: 36,
      students: 890,
      rating: 5,
      reviews: 156,
      category: "Advanced",
      features: [
        "Advanced Trading Strategies",
        "Portfolio Management",
        "Advanced Risk Management",
        "Market Analysis Mastery",
        "Live Trading Sessions"
      ]
    },
   
    {
      id: 4,
      title: "Options Greeks Mastery",
      description: "Deep dive into options Greeks and how to use them effectively in your trading for better risk management.",
      price: 3499,
      originalPrice: 12499,
      level: "Intermediate",
      duration: "6 weeks",
      lessons: 18,
      students: 672,
      rating: 4,
      reviews: 124,
      category: "Advanced",
      features: [
        "Delta, Gamma, Theta, Vega Analysis",
        "Greeks-based Strategies",
        "Hedging with Greeks",
        "Real-time Greeks Monitoring",
        "Greeks Calculator Tools"
      ]
    },
   
   
  
    {
      id: 7,
      title: "Day Trading Options",
      description: "Fast-paced options trading strategies for day traders looking to capitalize on intraday movements.",
      price: 699,
      originalPrice: 899,
      level: "Intermediate",
      duration: "8 weeks",
      lessons: 28,
      students: 734,
      rating: 4,
      reviews: 142,
      category: "Day Trading",
      features: [
        "Intraday Options Strategies",
        "Scalping Techniques",
        "High-Probability Setups",
        "Risk Management for Day Trading",
        "Platform Setup & Tools"
      ]
    },
   
  ];

  const beginnerCourses = courses.filter(course => course.level === 'Beginner');
  const intermediateCourses = courses.filter(course => course.level === 'Intermediate');
  const expertCourses = courses.filter(course => course.level === 'Expert');

  const courseFeatures = [
    {
      icon: BookOpen,
      title: "Comprehensive Materials",
      description: "In-depth course materials and resources"
    },
    {
      icon: Play,
      title: "Live Sessions",
      description: "High-quality video content with expert instruction"
    },
    {
      icon: Clock,
      title: "Lifetime Access",
      description: "Access your courses anytime, anywhere"
    },
    {
      icon: Award,
      title: "Certification",
      description: "Get certified upon course completion"
    }
  ];

  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    setIsVisible(true);
  }, []);

  return (
    <div className="bg-black min-h-screen">
      <div className={`transition-all duration-1000 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
        {/* Hero Section */}
        <div className="pt-24 pb-16">
          <div className={`max-w-7xl mx-auto px-6 text-center transition-all duration-1000 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
            <div className="mb-8">
              <h2 className="text-6xl md:text-7xl font-black bg-gradient-to-r from-purple-400 via-pink-400 to-purple-400 bg-clip-text text-transparent mb-6 leading-tight">
                Our Courses
              </h2>
              <p className="text-xl text-gray-400 max-w-2xl mx-auto leading-relaxed">
                Transform your trading journey with our comprehensive options trading programs designed for every skill level
              </p>
            </div>
          </div>
        </div>

        {/* Course Sliders */}
        <div className={`max-w-7xl mx-auto px-6 pb-20 transition-all duration-1000 delay-150 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
          <CourseSlider courses={intermediateCourses} />
        </div>

        {/* Course Features Section */}
        <div className={`max-w-7xl mx-auto px-6 pb-20 transition-all duration-1000 delay-300 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
          <div className="text-center mb-16">
            <h3 className="text-4xl font-bold text-white mb-4">Course Features</h3>
            <p className="text-gray-400 text-lg max-w-2xl mx-auto">Everything you need to become a successful options trader</p>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            {courseFeatures.map((feature, index) => {
              const Icon = feature.icon;
              return (
                <div
                  key={index}
                  className={`text-center group transition-all duration-700 transform ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}
                  style={{ transitionDelay: `${(index + 1) * 120}ms` }}
                >
                  <div className="w-16 h-16 bg-gradient-to-r from-purple-500 to-pink-500 rounded-2xl flex items-center justify-center mx-auto mb-4 group-hover:scale-110 transition-transform duration-300">
                    <Icon className="w-8 h-8 text-white" />
                  </div>
                  <h4 className="text-xl font-semibold text-white mb-2">{feature.title}</h4>
                  <p className="text-gray-400">{feature.description}</p>
                </div>
              );
            })}
          </div>
        </div>

        {/* CTA Section */}
        <div className={`max-w-7xl mx-auto px-6 pb-20 transition-all duration-1000 delay-500 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
          <div className="bg-gradient-to-r from-purple-500 to-pink-500 rounded-3xl p-12 text-center">
            <h3 className="text-4xl font-bold text-white mb-4">Ready to Start Trading?</h3>
            <p className="text-xl text-white/80 mb-8 max-w-2xl mx-auto">Join thousands of successful traders who have mastered options trading with our proven methods</p>
            <ReactRouterLink to="/enroll" >
              <button className="bg-white text-purple-600 px-8 py-4 rounded-xl font-semibold text-lg hover:shadow-lg hover:shadow-white/25 transition-all duration-300 transform hover:scale-105">
                Start Trading Now
              </button>
            </ReactRouterLink>
          </div>
        </div>
      </div>
      <style jsx>{`
        .scrollbar-hide {
          -ms-overflow-style: none;
          scrollbar-width: none;
        }
        .scrollbar-hide::-webkit-scrollbar {
          display: none;
        }
      `}</style>
    </div>
  );
};

export default CoursesPage;